'use client';
import { useMockDatabase } from '@/lib/data';
import { Header } from '@/components/layout/Header';
import MyScheduleList from '@/components/volunteer/MyScheduleList';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import type { Event } from '@/types';
import { Loader2, Download } from 'lucide-react';
import { Button } from '@/components/ui/button';
import AppShell from '@/components/layout/AppShell';

function generateIcsFile(events: Event[]): string {
  let icsString = [
    'BEGIN:VCALENDAR',
    'VERSION:2.0',
    'PRODID:-//WeaversLoom//VolunteerScheduler//EN',
  ].join('\r\n');

  events.forEach(event => {
    event.roles.forEach(role => {
        const startDate = event.eventDate;
        const endDate = new Date(startDate.getTime() + (60 * 60 * 1000)); // Assume 1 hour duration

        const formatToUTC = (date: Date) => {
            return date.toISOString().replace(/[-:]/g, '').split('.')[0] + 'Z';
        }

        const eventData = [
            'BEGIN:VEVENT',
            `UID:${event.id}-${role.id}@weaversloom.app`,
            `DTSTAMP:${formatToUTC(new Date())}`,
            `DTSTART:${formatToUTC(startDate)}`,
            `DTEND:${formatToUTC(endDate)}`,
            `SUMMARY:${role.roleName} - ${event.eventName}`,
            `DESCRIPTION:Your assigned role is ${role.roleName} for the event: ${event.eventName}.${event.notes ? `\\n\\nNotes: ${event.notes}` : ''}`,
            'END:VEVENT'
        ].join('\r\n');
        
        icsString += '\r\n' + eventData;
    });
  });

  icsString += '\r\nEND:VCALENDAR';
  return icsString;
}


export default function MySchedulePage() {
  const db = useMockDatabase();
  const { isLoggedIn, isAdmin, MOCK_VOLUNTEER_ID } = useAuth();
  const router = useRouter();
  const [myAssignments, setMyAssignments] = useState<Event[]>([]);
  const [isLoading, setIsLoading] = useState(true);

   useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (isAdmin) {
      router.push('/admin');
    }
  }, [isLoggedIn, isAdmin, router]);

  useEffect(() => {
    if (isLoggedIn && !isAdmin) {
      setIsLoading(true);
      db.getMyAssignments(MOCK_VOLUNTEER_ID).then(assignments => {
        const sorted = assignments.sort((a, b) => a.eventDate.getTime() - b.eventDate.getTime());
        setMyAssignments(sorted);
        setIsLoading(false);
      });
    }
  }, [isLoggedIn, isAdmin, db, MOCK_VOLUNTEER_ID]);
    
  const handleExport = () => {
    const upcomingAssignments = myAssignments.filter(event => event.eventDate >= new Date());
    const icsContent = generateIcsFile(upcomingAssignments);
    const blob = new Blob([icsContent], { type: 'text/calendar;charset=utf-8' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = 'my-schedule.ics';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  if (!isLoggedIn || isAdmin) {
    return null; // Or a loading/redirecting component
  }

  if (isLoading) {
    return (
      <AppShell>
        <div className="flex min-h-screen w-full flex-col bg-muted/40">
            <Header />
            <main className="flex-1 p-4 md:p-8 flex items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
            </main>
        </div>
      </AppShell>
    )
  }

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
          <div className="max-w-4xl mx-auto">
            <div className="flex items-center justify-between mb-6">
              <h1 className="text-3xl font-bold font-headline tracking-tight">
                  My Schedule
              </h1>
              <Button onClick={handleExport} variant="outline" disabled={myAssignments.length === 0}>
                  <Download className="mr-2 h-4 w-4" />
                  Export to Calendar
              </Button>
            </div>
            <MyScheduleList 
              assignments={myAssignments} 
            />
          </div>
        </main>
      </div>
    </AppShell>
  );
}
