'use client';
import { useState } from 'react';
import { format, isPast } from 'date-fns';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Calendar, Clock, User, MailQuestion } from 'lucide-react';
import type { Event, Role } from '@/types';
import { cn } from '@/lib/utils';
import RequestChangeModal from './RequestChangeModal';
import { generateRequestEmail } from '@/ai/flows/generate-request-email-flow';

interface ScheduleItemCardProps {
  event: Event;
  role: Role;
}

export default function ScheduleItemCard({ event, role }: ScheduleItemCardProps) {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const eventIsInPast = isPast(event.eventDate);

  return (
    <>
    <Card className={cn("transition-all", eventIsInPast && "opacity-60 bg-muted/50")}>
      <CardHeader>
        <div className="flex justify-between items-start">
            <div>
                <CardTitle className="font-headline text-xl">{event.eventName}</CardTitle>
                <CardDescription className="flex flex-wrap items-center gap-x-4 gap-y-1 text-sm mt-1">
                    <span className="flex items-center gap-1.5"><Calendar className="h-4 w-4 text-primary" /> {format(event.eventDate, 'EEEE, MMM d')}</span>
                    <span className="flex items-center gap-1.5"><Clock className="h-4 w-4 text-primary" /> {format(event.eventDate, 'p')}</span>
                </CardDescription>
            </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="flex items-center gap-2 bg-background p-3 rounded-md border">
            <User className="h-5 w-5 text-primary" />
            <div>
                <p className="text-sm text-muted-foreground">Your Role</p>
                <p className="font-semibold">{role.roleName}</p>
            </div>
        </div>
      </CardContent>
      {!eventIsInPast && (
        <CardFooter className="flex justify-end gap-2">
          <Button 
            onClick={() => setIsModalOpen(true)}
            variant="outline"
          >
            <MailQuestion className="mr-2 h-4 w-4" />
            Request Change
          </Button>
        </CardFooter>
      )}
    </Card>
    <RequestChangeModal
      isOpen={isModalOpen}
      onClose={() => setIsModalOpen(false)}
      event={event}
      role={role}
      sendRequestAction={generateRequestEmail}
    />
    </>
  );
}
